/* Noticias */
.NoticiaBox {
	margin: auto;
	margin-bottom: 10px;
	max-width: 1000px;
}

.NoticiaBox__Id {
	display: inline-block;
	padding: 20px;
	width: 100%;
	text-align: right;
}

.Noticia__Content--OverFlow {
	max-height: 400px;
	overflow: hidden;
}

.Noticia__Content {
	width: 100%;
	flex: 1;
}

/* Anuncios */
.Anuncio {
	min-height: 250px;
	padding: 15px 30px;
	display: flex;
	flex-direction: column;
}

.Anuncio__Title {
	font-size: 18px;
}

.Anuncio__Content {
	text-align: justify;
	width: 100%;
	flex: 1;
}

.Anuncio__Footer {
	font-style: italic;
	display: flex;
	justify-content: center;
	text-align: center;
}

.Anuncio__Id {
	text-align: right;
	width: 100%;
}

/* Img Preview */
.PreviewImgDivider {
	width: 90% !important;
	margin: auto !important;
	margin-top: 25px !important;
	margin-bottom: 20px !important;
}

.ImagePreview {
	max-width: 110px;
	max-height: 100px;
	min-width: 70px;
	min-height: 60px;
	cursor: pointer;
	margin-bottom: 10px;
	transition: .5s;
	
	&:hover {
		transform: scale(1.2);
	}
}

.ImagePreview__More {
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(0, 0, 0, 0.4);
	width: 100px;
	height: 100px;
	margin-bottom: 10px;
	cursor: pointer;
}

/* Visor Img React */
.VisorImgReactComponent {
	position: fixed;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.75);
	top: 0;
	left: 0;
	z-index: 100;
	transition: 0.5s;

	.VisorImgReactComponent__options {
		display: flex;
		justify-content: center;
		margin-bottom: 10px;
		z-index: 200;
	}

	.VisorImgReactComponent__download {
		margin-right: 100px;
	}

	.VisorImgReactComponent__controlls {
		position: absolute;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 100%;
		padding: 0px 10%;
		margin-top: 30px;
	}

	.VisorImgReactComponent__visor {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		height: 100%;
	}
	
	.imgShow {
		max-height: 80vh;
		max-width: 90vw;
	}
}